/* 
  Global Styles
*/
* {
  padding: 0;
  margin: 0;
  font-family: "Manrope", normal;
  color: theme("colors.000000");
}

/* Global Text Styles */
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .heading1,
  .heading2,
  .heading3,
  .heading4,
  .heading5,
  .heading6,
  .subtitle {
    font-weight: 700;
  }
  .heading1 {
    font-size: 56px;
    line-height: 58px;
    letter-spacing: 2px;
  }
  .heading2 {
    font-size: 40px;
    line-height: 44px;
    letter-spacing: 1.5px;
  }
  .heading3 {
    font-size: 32px;
    line-height: 36px;
    letter-spacing: 1.15px;
  }
  .heading4 {
    font-size: 28px;
    line-height: 38px;
    letter-spacing: 2px;
  }
  .heading5 {
    font-size: 24px;
    line-height: 33px;
    letter-spacing: 1.7px;
  }
  .heading6 {
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 1.3px;
  }
  .overline-text {
    font-size: 14px;
    line-height: 19px;
    letter-spacing: 10px;
    font-weight: 400;
  }
  .subtitle {
    font-size: 13px;
    line-height: 25px;
    letter-spacing: 1px;
  }
  .body {
    font-size: 15px;
    line-height: 25px;
    font-weight: 500;
  }
}

/* Button styles */
.button-1,
.button-2,
.button-3 {
  font-size: 13px;
  line-height: 18px;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.button-1 {
  background-color: theme("colors.D87D4A");
  border: 1px solid theme("colors.D87D4A");
  color: theme("colors.FFFFFF");
  padding: 15px 30px;
}
.button-1:hover {
  background-color: theme("colors.FBAF85");
  border: 1px solid theme("colors.FBAF85");
}
.button-2 {
  background-color: theme("colors.000000");
  border: 1px solid theme("colors.000000");
  color: theme("colors.FFFFFF");
  padding: 15px 30px;
}
.button-2:hover {
  background-color: theme("colors.4C4C4C");
  border: 1px solid theme("colors.4C4C4C");
}
.button-3 {
  color: rgba(theme("colors.000000"), 0.5);
  cursor: pointer;
}
.button-3:hover {
  color: rgba(theme("colors.D87D4A"), 0.5);
}
.button-4 {
  background-color: transparent;
  border: 1px solid theme("colors.000000");
  padding: 15px 30px;
}
.button-4:hover {
  background-color: theme("colors.000000");
  border: 1px solid theme("colors.000000");
  color: theme("colors.FFFFFF");
}

/* icons, facebook, twitter, instagram */
.icon:hover {
  filter: invert(50%) sepia(65%) saturate(416%) hue-rotate(338deg)
    brightness(97%) contrast(97%);
}

@media only screen and (max-width: 768px) {
  .heading1 {
    font-size: 36px;
    line-height: 40px;
    letter-spacing: 1.29px;
  }
}
